import React from 'react';
import photo from "../../../assets/images/mainPicture.png"
import {FlexWrapper} from "../../../components/FlexWrapper";
import {Container} from "../../../components/Container";
import {S} from "./Main_Styles"
import Typewriter from "typewriter-effect";
import Tilt from 'react-parallax-tilt';


export const Main: React.FC = () => {
    return (
        <S.Main id="main">
            <Container>
                <FlexWrapper align="center" justify="space-around" wrap="wrap">
                    <S.IntroWrapper>
                        <S.SmallText>Привіт!</S.SmallText>
                        <S.Name>Я <span>Анастасія Розанова</span></S.Name>
                        <h1>лікар-косметолог</h1>
                        <S.MainTitle>
                            <Typewriter
                                options={{
                                    strings: ['лікар-косметолог.', 'психолог.','фантастична жінка 🚀!!!'],
                                    autoStart: true,
                                    loop: true,
                                    delay: 80
                                }}
                            /></S.MainTitle>
                    </S.IntroWrapper>
                    <Tilt>
                        <S.PhotoWrapper>
                            <S.Photo src={photo}/>
                        </S.PhotoWrapper>
                    </Tilt>
                </FlexWrapper>
            </Container>
        </S.Main>
    );
};
