import React from 'react';
import {S} from "../HederMenu_Styles"

const headerMenuItems = [
    {title:"Гайд 'Щаслива шкіра'", href:"guide"},
    {title:"Послуги", href:"services"},
    {title:"Пацієнти", href:"patiens"},
    {title:"Відгуки", href:"testimony"},
    {title:"Контакт", href:"contacts"}
    ]

export const Menu: React.FC = () => {
    return (
        <ul>
            {headerMenuItems.map((item, index) => {
                return <S.MenuItem key={index}>
                    <S.Link href={`#${item.href}`}>{item.title}
                        <S.Mask>
                            <span>{item.title}</span>
                        </S.Mask>
                        <S.Mask>
                            <span>{item.title}</span>
                        </S.Mask>
                    </S.Link>
                </S.MenuItem>
            })}
        </ul>
    );
};
