import React from 'react';
import {S} from "./Patient_Styles"

type PatientPropsType = {
    src: string
    title: string
    description: string
}
export const Patient: React.FC<PatientPropsType> = (props: PatientPropsType) => {
    return (
        <S.Patient>
            <S.Image src={props.src} alt="Patient"/>
            <S.Description>
                <S.Title>{props.title}</S.Title>
                <S.Text>{props.description}</S.Text>
            </S.Description>
        </S.Patient>
    );
};
