import React, {useState} from 'react';
import styled from "styled-components";
import {SectionTitle} from "../../../components/SectionTitle";
import {TabMenu, TabsStatusyupe} from "./tabMenu/TabMenu";
import {FlexWrapper} from "../../../components/FlexWrapper";
import {Patient} from "./patient/Patient";
import oldBiden from "../../../assets/images/oldBiden.webp"
import youngBiden from "../../../assets/images/youngBiden.jpg"
import {Container} from "../../../components/Container";
import {AnimatePresence, motion} from "framer-motion"

const tabsItems:Array<{title:string,status: TabsStatusyupe }> = [
    {title:"Всі", status:"all"},
    {title:"Акне", status:"akne"},
    {title:"Ботокс", status:"botox"},
    {title:"Реджиранчик", status:"redj"}
]

const patientsData = [
    {type: "akne",src: oldBiden, title:"Лариса", description: "Прийшла замучена потаскана. Але ми зробили з нею канфетку, полюбуйтеся, тепер поцієнтка має друге життя"},
    {type: "botox", src:youngBiden, title:"Лариса", description: "Прийшла замучена потаскана. Але ми зробили з нею канфетку, полюбуйтеся, тепер поцієнтка має друге життя"},
    {type: "redj", src:oldBiden, title:"Лариса", description: "Прийшла замучена потаскана. Але ми зробили з нею канфетку, полюбуйтеся, тепер поцієнтка має друге життя"},
    {type:"botox", src:youngBiden, title:"Лариса", description: "Прийшла замучена потаскана. Але ми зробили з нею канфетку, полюбуйтеся, тепер поцієнтка має друге життя"},
]

export const Patiens: React.FC = () => {
    let [currentFilterStatus, setCurrentFilterStatus] = useState<TabsStatusyupe>("all")
    let filtredPatients = patientsData
    if (currentFilterStatus === "akne") {filtredPatients = patientsData.filter(p=>p.type === "akne")}
    if (currentFilterStatus === "botox") {filtredPatients = patientsData.filter(p=>p.type === "botox")}
    if (currentFilterStatus === "redj") {filtredPatients = patientsData.filter(p=>p.type === "redj")}

    function changeFilterStatus (value: TabsStatusyupe) {
        setCurrentFilterStatus(value)
    }
    return (
        <StyledPatiens id="patiens">
            <Container>
                <SectionTitle>Пацієнти</SectionTitle>
                <TabMenu tabsItems={tabsItems} currentFilterStatus={currentFilterStatus} changeFilterStatus={changeFilterStatus}/>
                <FlexWrapper justify="space-between" wrap="wrap">
                    <AnimatePresence>
                        {filtredPatients.map((p, index) => {
                            return (
                                <Patient key={index} src={p.src} title={p.title} description={p.description}/>
                            )
                        })}
                    </AnimatePresence>
                </FlexWrapper>
            </Container>
        </StyledPatiens>
    );
};
const StyledPatiens = styled.section`
    ${FlexWrapper} {
        gap: 30px;
    }
`
