import React from 'react';
import {Icon} from "../../components/icon/Icon";
import {FlexWrapper} from "../../components/FlexWrapper";
import {S} from "./Footer_Styles"

export const Footer:React.FC = () => {
    return (
        <S.Footer>
            <FlexWrapper direction="column" align="center">
                <S.Name>Анастасія</S.Name>
                <S.SocialList>
                    <li>
                        <S.SocialLink>
                            <Icon iconId="insta" viewBox={"0 0 23 23"}/>
                        </S.SocialLink>
                    </li>
                    <li>
                        <S.SocialLink>
                            <Icon iconId="telegram" viewBox={"0 0 24 24"}/>
                        </S.SocialLink>
                    </li>
                </S.SocialList>
                <S.CopyRight>© 2024 Anastasiya Rozanova, All Rights Reserved.</S.CopyRight>
            </FlexWrapper>
        </S.Footer>
    );
};