import React from 'react';
import styled from "styled-components";
import {theme} from "../../../../styles/Theme";
import {Link} from "../../../../components/Link";

export type TabsStatusyupe = "all" | "botox" | "redj" | "akne"
type TabMenuProps = {
    tabsItems: Array<{ title: string, status: TabsStatusyupe }>
    changeFilterStatus: (value: TabsStatusyupe) => void
    currentFilterStatus: TabsStatusyupe
}
export const TabMenu = (props: TabMenuProps) => {
    return (
        <StyledTabMenu>
            <ul>
                {props.tabsItems.map((item, index) => {
                    return <ListItem key={index}>
                        <Link active={props.currentFilterStatus === item.status} as={"button"} onClick={()=>{props.changeFilterStatus(item.status)}}>{item.title}</Link>
                    </ListItem>
                })}
            </ul>
        </StyledTabMenu>
    );
};

const StyledTabMenu = styled.nav`
    ul {
        display: flex;
        justify-content: space-between;
        max-width: 358px;
        width: 100%;
        margin: 0 auto 40px;
    }
`
const ListItem = styled.li`
    gap: 5px;
`
